"use client";
import ScrollAnimation from './ScrollAnimation';
import { useEffect, useState } from 'react';

const Hero = () => {
  const [typed, setTyped] = useState('');
  const [subtitle, setSubtitle] = useState('');
  const welcome = 'Welcome To My Portfolio';
  const nameLine = "I'm Andrew Anstendyk";
  const subLine = "I'm a web developer passionate about creating modern web applications";

  useEffect(() => {
    // typing for welcome
    let i = 0;
    const t1 = setInterval(() => {
      setTyped((prev) => prev + welcome[i]);
      i++;
      if (i >= welcome.length) clearInterval(t1);
    }, 40);

    // after welcome typed, type the name and subtitle
    const t2 = setTimeout(() => {
      let j = 0;
      const nameTimer = setInterval(() => {
        setSubtitle((prev) => prev + nameLine[j]);
        j++;
        if (j >= nameLine.length) {
          clearInterval(nameTimer);
          // type subline
          let k = 0;
          const subTimer = setInterval(() => {
            setSubtitle((prev) => prev + (k === 0 ? '\n' : '') + subLine[k]);
            k++;
            if (k >= subLine.length) clearInterval(subTimer);
          }, 20);
        }
      }, 60);
    }, welcome.length * 40 + 300);

    return () => {
      clearInterval(t1);
    };
  }, []);

  return (
    <section id="home" className="min-vh-100 d-flex align-items-center">
      <div className="container">
        <div className="row">
          <div className="col-lg-8 mx-auto text-center">
            <ScrollAnimation animation="fade-down">
              <h1 className="hero-title display-4 mb-4"><span className="type">{typed}</span><span className="cursor">|</span></h1>
            </ScrollAnimation>

            <ScrollAnimation animation="fade-up" delay={300}>
              <div className="hero-lines mb-4">
                {subtitle.split('\n').map((line, idx) => (
                  <p key={idx} className={idx === 0 ? 'hero-name' : 'hero-subtitle lead'}>{line}</p>
                ))}
              </div>
            </ScrollAnimation>

            <ScrollAnimation animation="zoom-in" delay={600}>
              <div className="d-flex justify-content-center gap-3">
                <a href="#projects" className="btn btn-glow btn-primary">View My Work</a>
                <a href="#contact" className="btn btn-glow btn-outline-primary">Contact Me</a>
              </div>
            </ScrollAnimation>
          </div>
        </div>
      </div>
    </section>
  );
};

export default Hero;